#adminmenu li#toplevel_page_jkit {
    .wp-menu-image img {
        width: 16px;
    }
    background-color: #0067ff;
    .dashicons-before {
        display: flex;
        align-items: center;
        justify-content: center;

        &::before {
            content: "";
            background-image: svg-load("../../../svg/jkit-icon.svg");
            background-repeat: no-repeat;
            padding: unset;
        }
    }

    a.essential-framework-child-menu {
        padding-left: 21px;
        background-color: rgba(255, 255, 255, 0.09);
    }

    a.essential-framework-child-menu.first,
    li.essential-framework-child-menu.last + li a {
        padding-top: 8px;
    }

    a.have-essential-framework-child-menu,
    a.essential-framework-child-menu.last {
        padding-bottom: 8px;
    }

    .jkit-review-menu span {
        color: #ffb900;
        margin-left: 5px;
    }
}

#essential-framework-admin-notice {
    .f-install-notice {
        .f-background {
            svg {
                width: 100vw;
            }
        }
    }
}

.f-install-notice {
    margin-top: 30px;
    border-radius: 5px;
    background: linear-gradient(270deg, #184d3d 0%, #000 100%);
    height: 248px;
    flex-shrink: 0;
    position: relative;
    overflow: hidden;
    transition: height 0.3s ease;

    .f-background {
        position: absolute;
        display: flex;

        svg {
            position: absolute;
        }
    }

    img {
        height: inherit;
        position: absolute;
        right: 0;
        pointer-events: none;
        z-index: 2;
    }

    .f-description {
        z-index: 2;
        position: relative;
        height: 175px;
        padding: 40px;

        .f-title {
            color: var(--White, #fff);
            font-family: Inter;
            font-size: 24px;
            font-style: normal;
            font-weight: 600;
            line-height: normal;
        }

        .f-subtitle {
            color: var(--Secondary-Text-Color, #cac7d6);
            font-family: Inter;
            font-size: 14px;
            font-style: normal;
            font-weight: 400;
            line-height: 20px;
            margin-top: 10px;
            width: 646px;
        }

        .f-control {
            display: flex;
            position: absolute;
            bottom: 40px;

            .f-install-button {
                display: inline-flex;
                padding: 12px 22px;
                justify-content: center;
                align-items: center;
                gap: 10px;
                border-radius: 3px;
                border: 1px solid rgba(255, 255, 255, 0.3);
                background: var(--accent, #9747FF);
                box-shadow: 0px 0px 40px 0px rgba(62, 230, 79, 0.9);
                color: var(--White, #fff);
                text-align: center;
                font-family: Inter;
                font-size: 14px;
                font-style: normal;
                font-weight: 500;
                line-height: normal;
                cursor: pointer;
                transition: background 0.3s ease, border 0.3s ease, box-shadow 0.3s ease;
                width: 160px;
                height: 20px;

                &.loading {
                    cursor: wait;
                    border-radius: 3px;
                    border: 1px solid rgba(255, 255, 255, 0.3);
                    background: #081f0a;
                    box-shadow: unset;

                    &:hover {
                        border-radius: 3px;
                        border: 1px solid rgba(255, 255, 255, 0.3);
                        background: #081f0a;
                        box-shadow: unset;
                    }
                }

                &.success {
                    cursor: auto;
                    border-radius: 3px;
                    border: 1px solid rgba(255, 255, 255, 0.3);
                    background: #32a352;
                    box-shadow: unset;
                    &:hover {
                        border-radius: 3px;
                        border: 1px solid rgba(255, 255, 255, 0.3);
                        background: #32a352;
                    }
                }

                &:hover {
                    border-radius: 3px;
                    border: 1px solid rgba(255, 255, 255, 0.3);
                    background: #32a352;
                    box-shadow: unset;
                }
            }

            .f-install-description {
                align-items: center;
                display: flex;
                margin-left: 20px;
                color: var(--Secondary-Text-Color, #cac7d6);
                text-align: center;
                font-family: Inter;
                font-size: 12px;
                font-style: normal;
                font-weight: 400;
                line-height: normal;

                &.reminder {
                    div {
                        align-items: center;
                        display: flex;
                        height: 30px;
                        cursor: pointer;
                        border-bottom: 1px solid var(--White, #fff);
                        color: var(--White, #fff);
                        font-family: Inter;
                        font-size: 12px;
                        font-style: normal;
                        font-weight: 400;
                        line-height: normal;
                    }
                }
            }
        }
    }
}

//Main dashboard
.profecient-dashboard-wrapper {
    margin-top: 30px;
    display: flex;
    gap: 35px;

    .profecient-card {
        background: #ffffff;
        border: 1px solid #00000000;
        border-radius: 5px;
        box-shadow: 0px 25px 50px rgba(191, 197, 208, 0.1);
    }

    .profecient-dashboard-left {
        display: flex;
        flex-direction: column;
        gap: 35px;

        .profecient-dashboard-wellcome {
            width: 884px;
            height: 145px;

            .profecient-dashboard-wellcome-content {
                margin: 30px;
                display: flex;
                position: relative;

                .description {
                    width: 580px;
                    display: flex;
                    flex-direction: column;
                    gap: 10px;

                    .title {
                        font-family: "Inter";
                        font-style: normal;
                        font-weight: 600;
                        font-size: 22px;
                        line-height: 29px;
                        color: #0f1538;
                        margin: unset;
                    }

                    span {
                        font-family: "Inter";
                        font-style: normal;
                        font-weight: 400;
                        font-size: 14px;
                        line-height: 23px;
                        color: #7d8292;
                        width: 500px;
                    }
                }

                .version {
                    position: absolute;
                    right: 0;
                    width: 81px;
                    height: 20px;
                    background: #edb460;
                    border-radius: 100px;
                    display: flex;
                    align-items: center;

                    span {
                        font-family: "Inter";
                        font-style: normal;
                        font-weight: 500;
                        font-size: 10px;
                        line-height: 12px;
                        color: #ffffff;
                        margin: 0 auto;
                    }
                }
            }
        }

        .profecient-dashboard-thanks {
            width: 884px;
            height: 275px;

            .profecient-dashboard-thanks-content {
                margin: 30px;
                display: flex;
                position: relative;
                gap: 35px;

                img {
                    border-radius: 5px;
                    width: 325px;
                    user-select: none;
                    pointer-events: none;
                }

                .description {
                    width: 580px;
                    display: flex;
                    flex-direction: column;
                    gap: 10px;
                    margin-right: 15px;

                    .title {
                        font-family: "Inter";
                        font-style: normal;
                        font-weight: 600;
                        font-size: 26px;
                        line-height: 45px;
                        color: #0f1538;
                        margin-bottom: unset;
                    }

                    span {
                        font-family: "Inter";
                        font-style: normal;
                        font-weight: 400;
                        font-size: 14px;
                        line-height: 20px;
                        color: #7d8292;

                        a {
                            color: #0067ff;
                        }
                    }
                }
            }
        }

        .profecient-dashboard-submenu {
            display: flex;
            gap: 35px;
            flex-direction: row;

            .profecient-dashboard-menu-item {
                position: relative;
                width: 272px;
                height: 250px;
                overflow: hidden;
                background: radial-gradient(130.63% 141.42% at 100% 0%, #e4ffcf 0%, #fff 73.44%);

                &.profecient-card {
                    box-shadow: none;
                    transition: box-shadow 0.3s ease;
                    border: none;

                    &:hover {
                        box-shadow: 0px 10px 20px 0px rgba(191, 197, 208, 0.3);
                    }
                }

                .profecient-dashboard-menu-icon {
                    width: 60px;
                    height: 60px;
                    position: absolute;
                    right: 20.5px;
                    top: 14px;

                    img {
                        width: 60px;
                        height: 60px;
                    }
                }

                .profecient-dashboard-menu-content {
                    margin: 70px 24px 24px;

                    .description {
                        display: flex;
                        flex-direction: column;
                        gap: 10px;

                        .title {
                            font-family: "Inter";
                            font-style: normal;
                            font-weight: 600;
                            font-size: 20px;
                            line-height: 24px;
                            display: flex;
                            align-items: center;
                            color: #0f1538;
                            margin: unset;
                        }

                        span {
                            font-family: "Inter";
                            font-style: normal;
                            font-weight: 400;
                            font-size: 14px;
                            line-height: 20px;
                            display: flex;
                            align-items: center;
                            color: #7d8292;
                            margin-bottom: 10px;
                        }

                        a {
                            height: 41px;
                            background: #e3ffec;
                            border: 1px solid #bee1c8;
                            transition: background 0.3s ease, border 0.3s ease;
                            border-radius: 4px;
                            display: flex;
                            width: fit-content;
                            text-decoration: none;

                            &:hover {
                                background: #9747FF;
                                border: 1px solid #9747FF;
                                
                                span {
                                    color: #fff;
                                }
                            }

                            span {
                                font-family: "Inter";
                                font-style: normal;
                                font-weight: 500;
                                font-size: 14px;
                                line-height: 17px;
                                display: flex;
                                align-items: center;
                                text-align: center;
                                color: #9747FF;
                                margin: 0 22px;
                            }
                        }
                    }
                }
            }
        }
    }

    .profecient-dashboard-right {
        display: flex;
        flex-direction: column;
        gap: 35px;

        .profecient-dashboard-rating {
            width: 290px;
            height: 393px;
            border: none;

            img {
                width: 290px;
                height: 393px;
                user-select: none;
            }

            a {
                &:focus {
                    box-shadow: none;
                }
            }
        }

        .profecient-dashboard-subscribe {
            width: 290px;
            height: 312px;

            .profecient-dashboard-subscribe-wrapper {
                margin: 30px;

                .description {
                    display: flex;
                    flex-direction: column;
                    gap: 10px;

                    .title {
                        text-align: center;
                        font-family: "Inter";
                        font-style: normal;
                        font-weight: 600;
                        font-size: 20px;
                        line-height: 24px;
                        display: flex;
                        align-items: center;
                        color: #0f1538;
                        margin-bottom: unset;
                        margin-top: 5px;
                        margin-left: auto;
                        margin-right: auto;
                    }

                    span {
                        text-align: center;
                        font-family: "Inter";
                        font-style: normal;
                        font-weight: 400;
                        font-size: 14px;
                        line-height: 20px;
                        display: flex;
                        align-items: center;
                        color: #7d8292;
                    }
                }

                .profecient-dashboard-form {
                    width: 100%;
                    margin-top: 20px;

                    input {
                        &.profecient-dashboard-email {
                            height: 45px;
                            background: #ffffff;
                            border: 1px solid #d0d5dd;
                            box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
                            border-radius: 4px;
                            width: 100%;
                            margin: 15px 0;
                            padding: 0 15px;

                            &::placeholder {
                                color: #82859d;
                                font-family: "Inter";
                                font-style: normal;
                                font-weight: 400;
                                font-size: 14px;
                                line-height: 17px;
                            }
                        }
                    }

                    .loading {
                        display: flex;
                        align-items: center;
                        svg {
                            color: #ffffff;
                        }
                    }

                    a {
                        background: var(--accent, #9747FF);
                        border: 1px solid var(--accent, #9747FF);
                        height: 41px;
                        border-radius: 4px;
                        display: flex;
                        text-decoration: none;
                        cursor: pointer;
                        transition: background 0.3s ease, border 0.3s ease;

                        &:hover {
                            background: #32a352;
                            border: 1px solid #32a352;
                        }

                        > span {
                            text-align: center;
                            font-family: "Inter";
                            font-style: normal;
                            font-weight: 400;
                            font-size: 14px;
                            line-height: 20px;
                            display: flex;
                            align-items: center;
                            color: #ffffff;
                            margin: 0 auto;
                        }
                    }

                    > span {
                        text-align: center;
                        margin: 10px auto;
                        display: block;
                        font-family: "Inter";
                        font-style: normal;
                        font-weight: 400;
                        font-size: 11px;
                        line-height: 12px;
                        text-align: center;
                        color: #82859d;
                    }
                }
            }
        }
    }
}
